<template>
	<view class="container">
		<NavBar></NavBar>
		<view class="main-container">
			<view class="search-container">
				<SearchBar @input='search_history'></searchbar>
			</view>
			<view class="history-list">
				<view class="history-item" v-for="(item, index) in search_list" :key="index">
					<image class="history-icon" src="/static/icons/history-placeholder.png"></image>
					<view class="history-info">
						<text class="history-name">{{ item.name }}</text>
						<text class="history-time">{{ item.time }}</text>
					</view>
					<view class="checkbox"></view>
				</view>
				<text class="more-history" @click="enter_history_page">更多历史 ➔</text>
			</view>
		</view>
		<TabBar currentPath='/pages/index/home'></TabBar>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				search_value: '',
				drawTime: '23小时59分59秒',
				recover: 'annoy',
				data_list: [{
						name: '1111',
						time: '2024/11/14',
						path: '../../static/c1.png',
						description: '111'
					},
					{
						name: '1',
						time: '2024/11/14',
						path: '../../static/c2.png',
						description: '111222'
					},
					{
						name: '1',
						time: '2024/11/14',
						path: '../../static/c2.png',
						description: '111222'
					},
					{
						name: '1',
						time: '2024/11/14',
						path: '../../static/c2.png',
						description: '111222'
					},
					{
						name: '1',
						time: '2024/11/14',
						path: '../../static/c2.png',
						description: '111222'
					}
				],
				search_list: [],
				max_search_items: 3
			}

		},
		onLoad() {
			uni.hideTabBar();
			this.search_list = this.data_list.slice(0, this.max_search_items);
		},
		methods: {
			enter_history_page() {
				uni.navigateTo({
					url: '/pages/history/history'
				})
			},
			search_history(key) {
				this.search_list = [];
				for (var i = 0; i < Math.min(this.max_search_items, this.data_list.length); i++) {
					var item = this.data_list[i];
					var name = item.name;
					var time = item.time;
					if (name.includes(key) || time.includes(key)) {
						this.search_list.push(item)
					}
				}
			}
		}
	}
</script>

<style scoped>
	@import './home.css'
</style>